    </AppHeader>

    <div class="form-page">
      <div class="filters">
        <div class="filters-label"><span>*</span> 货架编号:</div>
        <div class="date-filters">
          <input class="text-left" type="text" v-model="code" placeholder="请选择" />
        </div>
      </div>

      <div class="filters">
        <div class="filters-label"><span>*</span> 部门:</div>
        <div class="date-filters">
          <input class="text-left" type="text" v-model="code" placeholder="请选择" />
        </div>
      </div>

      <div class="filters">
        <div class="filters-label"><span>*</span> 大类code:</div>
        <div class="date-filters">
          <input class="text-left" type="text" v-model="code" placeholder="请选择" />
        </div>
      </div>

      <div class="filters">
        <div class="filters-label"><span>*</span> 中类code:</div>
        <div class="date-filters">
          <input class="text-left" type="text" v-model="code" placeholder="请选择" />
        </div>
      </div>

      <div class="filters">
        <div class="filters-label"><span></span> 小类code:</div>
        <div class="date-filters">
          <input class="text-left" type="text" v-model="code" placeholder="请选择" />
        </div>
      </div>

      <div class="filters">
        <div class="filters-label"><span>*</span> 货架占比:</div>
        <div class="date-filters">
          <input class="text-left" type="text" v-model="code" placeholder="请选择" />
        </div>
      </div>

      <div class="filters">
        <div class="filters-label"><span>*</span> 商品详情:</div>
        <div class="date-filters">
          <input class="text-left" type="text" v-model="code" placeholder="请选择" />
        </div>
      </div>

      <div class="filters">
        <div class="filters-label"><span>*</span> 图片:</div>
        <div class="date-filters">
          <input class="text-left" type="text" v-model="code" placeholder="请选择" />
