<script>
import Vue from "vue";
import {
  Search,
  Cell,
  CellGroup,
  Icon,
  Field,
  DatetimePicker,
  Button,
  Popup
} from "vant";

import ElementUI from "element-ui";

Vue.use(Search)
  .use(Cell)
  .use(CellGroup)
  .use(Field)
  .use(DatetimePicker)
  .use(Popup)
  .use(Button)
  .use(Icon);
Vue.use(ElementUI);

// @ is an alias to /src
import AppHeader from "@/components/AppHeader.vue";

export default {
  name: "AddGoodsShelvesDetails",
  components: {
    AppHeader
  },

  data() {
    return {
      value: '',
      columns: ['杭州', '宁波', '温州', '嘉兴', '湖州'],
      showPicker: false,
      code: ""
    };
  },
  mounted() {
    this.get_defaultData();
    this.get_data();
  },
  methods: {
    onConfirm(value) {
      this.value = value;
      this.showPicker = false;
    },
    confirmSearch(val) {
      this.searchKey = val;
      this.get_data();
    },
    pageCurrentChange(val) {
      console.log(val);
      this.currentPage = val;
      this.currentData = this.resultData.slice(
        this.currentPage * 10 - 10,
        this.currentPage * 10
      );
      console.log(this.currentPage);
    },
    get_defaultData() {
      this.baseURL = this.$store.getters.getBaseURL;
      this.datetime = this.formatDate.pastDate(2, "-");
    },
    // 获取数据
    get_data() {
      this.toast.loading({
        message: "加载中...",
        forbidClick: true,
        loadingType: "spinner",
        duration: 0
      });
      this.getStoreInfo = this.$store.getters.getStoreInfo;
      let params = {
        storeId: this.getStoreInfo.gid,
        searchKey: this.searchKey
      };
      this.serverHost
        .fetchPost("/shelf/getRackTable", params)
        .then(res => {
          this.toast.clear();
          console.log(res);
          const resultData = res.data.aaData;
          if (resultData.length == false) {
            this.hasData = false;
          } else {
            this.hasData = true;
            this.resultData = resultData;
            this.currentData = resultData.slice(
              this.currentPage * 10 - 10,
              this.currentPage * 10
            );
            this.recordcount = resultData.length;
          }
          console.log(this.currentData);
        })
        .catch(res => {
          this.toast.clear();
          console.log(res);
        });
    }
  }
};
</script>

